body {
  margin: 0;
  padding: 0;
}


.route-ctr{
  width: 100vw;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-content: center;

}

.route-ctr section:first-child{
  margin-top: 0;
  padding-top: 0;
}

.route-ctr section:last-child{
  margin-bottom:0;
}
#nav{
  width: 100vw;
  height: 4rem;
  display: flex;
  flex-flow: row-reverse nowrap;
  justify-content: space-between;
  align-items: center;
  background: white;
  -webkit-transform: translateX(-0.5rem);
          transform: translateX(-0.5rem);
}

.nav__list{
  min-width: 18rem;
  margin: 15px 0;
  /* padding: 0 30px 0 0px; */
  list-style: none;
}

.nav__item{
  display: inline-block;
  margin: 0;
  padding: 10px 10px;
}

.nav__item > * {
  font-family: 'Helvetica', Arial sans-serif;
  margin: 0 10px;
  padding: 10px 5px;
  font-size: 1.5rem;
  font-weight: 600;
  text-decoration: none;
  color: #187945;
}
.nav__item:hover {
  padding: 10px 8px;
  border-right: 2px solid rgba(99, 99, 99, 0.25);
  border-left: 2px solid rgba(99, 99, 99, 0.25);
  background: rgba(166, 206, 161, 0.384);
  color: #0f4b2b;
}
.nav__item:first-child:hover {
  padding: 10px 9px;
  border-left: 0px solid rgba(99, 99, 99, 0.5);
}
.nav__item:last-child:hover {
  padding: 10px 9px 10px 8px;
  border-right: 1px solid rgba(99, 99, 99, 0);
}

/* TODO Fix it so there's an explicit double column card selector rule */

.card{
  width: 33%;
  margin: 0 10px;
  background:  rgb(255, 255, 255);
}

.card__header{
  width: 100%;
  margin: 0 5px;
  padding: 0;
}

.card__media img{
  max-width: 100%;
  max-height: 65vh;
  height: auto;
}

.card__content{
  min-width: 47%;
  max-width: 85%;
  margin: 0 5px;
}

/* NOTE Card Modifiers */

.card--shadow{
  box-shadow: 4px 4px 3px rgba(0, 0, 0, 0.1);
}

.card--basic{
  border: 1px solid rgb(211, 211, 211);
  background: white;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: flex-start;
  box-shadow: 4px 4px 3px rgba(0, 0, 0, 0.1);
}


.card--lg-no-border{
  max-width: 70%;
  background: rgb(255, 255, 255);
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: flex-start;
}

.card--media-top{
  width: 20rem;
  border: 1px solid rgb(209, 209, 209);
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: center;
  box-shadow: 4px 4px 3px rgba(0, 0, 0, 0.1);
}

/* NOTE Modifier based sizing */

.card--basic > .card__media{
  width: 45%;
  margin: 0 5px;
  align-self: center;
}

.card--media-top img{
  width: 20rem;

}
.media--sm{
  width: 30%;
}

.media--md{
  width: 45%;
}

.media--lg{
  width: 65%;
}

.media > *{
  max-width: 100%;
  height: auto;
}

.media{
  margin: 0 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.media--video-std{
  background:rgb(226, 226, 226);
}

.media--video-std > * {
  max-width: 100%;
  height: 315px;
}

.card--landing{
  position: relative;
  top: -2rem;
  max-width: 250px;
  height: 250px;
  margin: 0 20px;
  -webkit-transform: skew(20deg);
          transform: skew(20deg);
  background: transparent;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}

.section{
  width: 100%;
  min-height: 60vh;
  padding: 20px 0;
  margin: 25px 0;
}

.section--bg-light{
  background: rgba(160, 201, 160, 0.151);
}

.section--shadow{
  box-shadow: 4px 4px 3px rgba(0, 0, 0, 0.1);
}


.heading{
  font-family: 'Liberation Sans', Arial sans-serif;
  width: 100%;
  padding: 10px 20px;
}

.heading--subtitle{
  margin-left: 20px;
}

.heading--main{
  width: 100vw;
  text-align: left;
  font-size: 2rem;
  margin: 0px 0 15px 0;
}


.heading--card{
  margin: 0;
}
:root{
  --shape-pos: absolute;
}

.p{
  font-family: 'Helvetica', Arial sans-serif;
}


iframe{
  border: 0
}

.heading--landing{
  text-align: center;
  -webkit-transform: skew(-20deg);
          transform: skew(-20deg);
  margin:0;
}

.section--one-col{
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}

.section--double-col{
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-items: center;
}

.section--double-col-rev{
  display: flex;
  flex-flow: row-reverse wrap;
  justify-content: space-around;
  align-items: center;
}


.section--landing{
  height: 100vh;
  width: 100%;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card--landing{
  max-width: 250px;
  height: 250px;
  margin: 0 20px;
  -webkit-transform: skew(20deg) translateY(-2rem) translateX(1rem);
          transform: skew(20deg) translateY(-2rem) translateX(1rem);
  background: transparent;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}

.section--bg-lin-grad{
  background: linear-gradient(to bottom, white 30%, rgb(160, 201, 160));
}


.section--competition{
  padding-bottom: 5%;
}

#competition-list{
  background: rgb(255, 255, 255);
  padding: 10px 0;
  border: 1px solid rgba(211, 211, 211, 0.2);
  box-shadow: 4px 4px 2px rgba(0, 0, 0, 0.1);
}

.heading--comp{
  font-size: 2rem;
  text-align: center;
  padding-left: 0;
  padding-right: 0;
}

.cntnt-list--competition{
  width: 100%;
  list-style: none;
  margin: 0;
  padding:0;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;

}

.cntnt-list__item{
  margin: 10px 0;
  font-size: 1.75rem;
  text-align: center;
}
.cntnt-list__item a{
  font-family: 'Helvetica', Arial sans-serif;
  text-decoration: none;
  color: #187945;
}
.cntnt-list__item a:hover{
  color: #05331a;
  font-weight: 550;
}
.media--shapes{
  z-index: -1;
  width: 100%;
  height: 100%;
  margin: 0;
  position: absolute;
  position: var(--shape-pos, absolute);
  top: 0;
  left: 0;
  opacity: 0.7;
  display: grid;
  grid-template-columns: 20% 20% 20% calc(20% - 50px) 20%;
  grid-template-rows: 20% 20% 20% 20% 20%;
  grid-template-areas:
    ". . . . ."
    ". circle3 . circle2 ."
    ". . parallelogram . ."
    ". triangle . circle1 ."
    ". . . . ."
}

.comp-grid{
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 300px 1fr 1fr ;
  grid-template-rows: 0rem 1fr 1fr ;
  grid-column-gap: 5rem ;
  grid-row-gap: 5rem ;
  grid-template-areas:
    ". . . . ."
    ". top-left list top-right ."
    ". bot-left list bot-right ."
}

.comp-grid--heading{
  grid-area: header
}

.comp-grid--TL{
  grid-area: top-left;
}
.comp-grid--TR{
  grid-area: top-right;
}
.comp-grid--BL{
  grid-area: bot-left
}
.comp-grid--BR{
  grid-area: bot-right;
}

.comp-grid--center{
  grid-area: list;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}

.comp-grid__card{
  margin:0 auto;
}
#circle--1{
  grid-area: circle1;
  content: '';
  width: 80px;
  height: 80px;
  border-radius: 40px;
  background: rgba(155, 30, 185, 0.4);
  -webkit-transform: translateX(4rem);
          transform: translateX(4rem);
  box-shadow: 4px 4px 3px rgba(0, 0, 0, 0.1);
}

#circle--2{
  grid-area: circle2;
  content: '';
  width: 100px;
  height: 100px;
  border-radius: 50px;
  -webkit-transform: translate(60px 60px);
          transform: translate(60px 60px);
  background: rgba(150, 55, 140, 0.4);
  box-shadow: 4px 4px 3px rgba(0, 0, 0, 0.1);
}

#circle--3{
  grid-area: circle3;
  content: '';
  width: 125px;
  height: 125px;
  border-radius: 62.5px;
  background: rgba(110, 130, 205, 0.4);
  box-shadow: 4px 4px 3px rgba(0, 0, 0, 0.1);
}

#triangle--md{
  grid-area: triangle;
  content: '';
  width: 0;
  height: 0;
  background: transparent;
  border-bottom: 87.5px solid rgba(190, 30, 30, 0.6);
  border-right: 196.25px solid transparent;
  border-left: 200px solid transparent;
  border-top: 0;
  -webkit-transform: rotate(-110deg) translateY(2rem);
          transform: rotate(-110deg) translateY(2rem);
  -webkit-transform-origin: 40% 78%;
          transform-origin: 40% 78%;
}

.parallelogram {
  width: 300px;
  height: 250px;
  -webkit-transform: skew(20deg);
          transform: skew(20deg);
  background: rgba(70, 190, 75, 0.4);
  box-shadow: 4px 4px 3px rgba(0, 0, 0, 0.1);
}

.shape-wrapper{
  grid-area: parallelogram;
  display: flex;
  justify-content: center;
  align-items: center;
}
